import React, { useState } from "react";
import "./App.css";
import UserInfo from "./component/UserInfo/UserInfo";
import UserItem from "./component/UserInfo/UserItem";
const usersArray = [
  {
    username: "aliraza",
    age: 20,
  },
  {
    username: "mama",
    age: 21,
  },
  {
    username: "heavy",
    age: 27,
  },
];
const App = () => {
  const usersArray = [
    {
      username: "aliraza",
      age: 20,
    },
    {
      username: "alimuhammad",
      age: 21,
    },
    {
      username: "amjad",
      age: 27,
    },
  ];
  const [userAges, setUserAge] = useState(usersArray);
  const AllDetails = (data) => {
    setUserAge((prevData) => {
      return [data, ...prevData];
    });
  };
  return (
    <div className="app">
      <UserInfo onClickHandler={AllDetails} />
      {userAges.map((userAge) => {
        return <UserItem items={userAge} />;
      })}
    </div>
  );
};

export default App;
